export default {
    name: 'app-write-off-code',
    props: {
        hidden: {
            type: Boolean,
            default() {
                return false;
            }
        },
        itemId: {
            type: String,
            default() {
                return '-1';
            }
        }
    },
    data() {
        return {
            file_path: '',
        }
    },
    watch: {
        hidden: {
            handler: function(v) {
                if (v === true) {
                    this.$request({
                        url: this.$api.book.clerk_code,
                        data: {
                            id: this.itemId,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            this.file_path = response.data.file_path;
                        }
                    })
                }
            }
        }
    },
    methods: {
        hiddenHandler() {
            this.$emit('hiden', false);
            this.file_path = '';
        }
    }
}
